import React from 'react'
import { dateToString, translate, pad } from '../../lib/utils'

const getMonthName = (date) => {
  return date.toLocaleString('en-US', { month: 'short' })
}

function DateAxis ({ font, startStamp, endStamp, chartW, chartX, y, fontSize = 7 }) {
  const year = new Date(0)
  year.setFullYear(year.getFullYear() + 1)
  const yearMs = year.getTime()
  const dayMs = 1000 * 60 * 60 * 24
  const diff = (endStamp - startStamp) * 1000
  const times = []
  const startDate = new Date(startStamp * 1000)
  const endDate = new Date(endStamp * 1000)

  if (diff > 2 * yearMs) { // over 2 years
    const current = (new Date(startDate.getTime()))
    current.setMonth(0)
    current.setDate(1)
    startDate.setDate(startDate.getDate() - 30) // Increase the span a bit if its close
    endDate.setDate(endDate.getDate() + 30)
    while (current.getTime() < endDate.getTime()) {
      if (startDate.getTime() < current.getTime() && current.getTime() < endDate.getTime()) {
        times.push({ title: current.getFullYear(), timeStamp: current.getTime() })
      }
      current.setFullYear(current.getFullYear() + 1)
    }
  } else if (diff > yearMs) { // 2 years - 1 years
    console.log('over 1 year')

    const current = (new Date(startDate.getTime()))
    current.setDate(1)
    startDate.setDate(startDate.getDate() - 15) // Increase the span a bit if its close
    endDate.setDate(endDate.getDate() + 15)
    while (current.getTime() < endDate.getTime()) {
      if (startDate.getTime() < current.getTime() && current.getTime() < endDate.getTime()) {
        times.push({ title: current.getFullYear() + ' ' + getMonthName(current), timeStamp: current.getTime() })
      }
      current.setMonth(current.getMonth() + 1)
    }
  } else if (diff > 2 * yearMs / 12) { // 1 year - 2 months
    console.log('over 3 months')

    const current = (new Date(startDate.getTime()))
    current.setDate(1)
    startDate.setDate(startDate.getDate() - 15) // Increase the span a bit if its close
    endDate.setDate(endDate.getDate() + 15)
    while (current.getTime() < endDate.getTime()) {
      if (startDate.getTime() < current.getTime() && current.getTime() < endDate.getTime()) {
        times.push({ title: getMonthName(current), timeStamp: current.getTime() })
      }
      current.setMonth(current.getMonth() + 1)
    }
  } else if (diff > dayMs * 1) { // 2 months - 1 days
    console.log('over 30 days')
    const toAdd = (diff > 1 * yearMs / 12) ? 2 : 1
    const current = (new Date(startDate.getTime()))
    startDate.setHours(0)
    endDate.setHours(23)
    while (current.getTime() < endDate.getTime()) {
      times.push({ title: dateToString(current), timeStamp: current.getTime() })
      current.setDate(current.getDate() + toAdd)
    }
  } else { // 1 days - 0
    console.log('print hours...')

    const current = (new Date(startDate.getTime()))
    current.setMinutes(0)
    current.setSeconds(0)
    startDate.setSeconds(0)
    endDate.setMinutes(endDate.getMinutes() + 10)
    endDate.setSeconds(0)
    while (current.getTime() <= endDate.getTime()) {
      times.push({ title: pad(current.getHours()) + ':' + pad(current.getMinutes()), timeStamp: current.getTime() })
      current.setHours(current.getHours() + 1)
    }
  }

  return (
    <g transform={translate(chartX, y)}>
      {/* <rect width={chartW} height={10} fill='red' /> */}
      {times.map((dateObj) => <text fontFamily={font} textAnchor='middle' fontSize={fontSize} fill='#777' x={chartW * ((dateObj.timeStamp - startStamp * 1000) / diff)} key={'graphDate' + dateObj.timeStamp}>{dateObj.title}</text>)}
    </g>
  )
}
export default DateAxis
