import React from 'react'
function ChartLines ({ font, chartX, chartY, chartW, chartH, minValue, maxValue, textColor = '#777', fontSize = '7' }) {
  const ΔValue = maxValue - minValue
  const chartLineColor = '#ddd'

  return (
    <g>
      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 0.0)} y2={chartY + (chartH * 0.0)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 0.0)}>{minValue + ΔValue * 1.0}</text>

      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 0.2)} y2={chartY + (chartH * 0.2)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 0.2)}>{minValue + ΔValue * 0.8}</text>

      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 0.4)} y2={chartY + (chartH * 0.4)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 0.4)}>{minValue + ΔValue * 0.6}</text>

      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 0.6)} y2={chartY + (chartH * 0.6)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 0.6)}>{minValue + ΔValue * 0.4}</text>

      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 0.8)} y2={chartY + (chartH * 0.8)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 0.8)}>{minValue + ΔValue * 0.2}</text>

      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * 1.0)} y2={chartY + (chartH * 1.0)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * 1.0)}>{minValue + ΔValue * 0.0}</text>
    </g>
  )
}
export default ChartLines
