import React from 'react'
import { dateToString, translate, scale } from '../../lib/utils'

function Titles ({ width, height, title, subTitle, font, showDate, startDate, endDate }) {
  const startDateObj = new Date(startDate)
  const endDateObj = new Date(endDate)
  const offset = (130 * 3 + height * 0.115) / 4
  // 30x40cm is 1133.85826656
  const textSizeBoost = (height - 1133.85826656) / 1500 + 1

  return (
    <g transform={translate(0, offset) + scale(textSizeBoost, width)} transformOrigin='50% 50%'>
      <text x={width / 2} y='0' fill='#000' fontFamily={font} fontWeight='bold' fontSize={26} textAnchor='middle' alignmentBaseline='hanging'>{title}</text>
      <text x={width / 2} y='40' fill='#666' fontFamily={font} fontSize={15} textAnchor='middle' alignmentBaseline='hanging'>{subTitle}</text>
      {showDate && <text x={width / 2} y='64' fill='#666' fontFamily={font} fontSize={7} textAnchor='middle' alignmentBaseline='hanging'>{dateToString(startDateObj)} - {dateToString(endDateObj)}</text>}
    </g>
  )
}
export default Titles
